
import { useState } from 'react';
import { useBudget } from '../context/BudgetContext';

const BudgetForm: React.FC = () => {
  const { setExpenses, setIncome, categories } = useBudget();
  const [type, setType] = useState<'expense' | 'income'>('expense');
  const [amount, setAmount] = useState<number | ''>('');
  const [category, setCategory] = useState<string>('');
  const [description, setDescription] = useState<string>('');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const newEntry = { amount, category, description, date: new Date() };

    if (type === 'expense') {
      setExpenses(prev => [...prev, newEntry]);
    } else {
      setIncome(prev => [...prev, newEntry]);
    }

    // Clear form
    setAmount('');
    setCategory('');
    setDescription('');
  };

  return (
    <form onSubmit={handleSubmit}>
      <div>
        <label>Type:</label>
        <select onChange={(e) => setType(e.target.value as 'expense' | 'income')} value={type}>
          <option value="expense">Expense</option>
          <option value="income">Income</option>
        </select>
      </div>
      <div>
        <label>Amount:</label>
        <input
          type="number"
          value={amount}
          onChange={(e) => setAmount(Number(e.target.value))}
        />
      </div>
      <div>
        <label>Category:</label>
        <select onChange={(e) => setCategory(e.target.value)} value={category}>
          {categories.map((cat, idx) => (
            <option key={idx} value={cat}>{cat}</option>
          ))}
        </select>
      </div>
      <div>
        <label>Description:</label>
        <input
          type="text"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
        />
      </div>
      <button type="submit">Add {type === 'expense' ? 'Expense' : 'Income'}</button>
    </form>
  );
};

export default BudgetForm;