import React, { createContext, useContext, useState } from 'react';

interface Expense {
    amount: any;
    category: string;
    description: string;
    date: Date;
  }

interface Income {
  amount: any;
  category:string;
  description: string;
  date: Date;
}

interface BudgetContextType {
  expenses: Expense[];
  setExpenses: React.Dispatch<React.SetStateAction<Expense[]>>;
  income: Income[];
  setIncome: React.Dispatch<React.SetStateAction<Income[]>>;
  categories: string[];
  setCategories: React.Dispatch<React.SetStateAction<string[]>>;
  budgetLimits: { [key: string]: number };
  setBudgetLimits: React.Dispatch<React.SetStateAction<{ [key: string]: number }>>;
}

// Context'i oluşturuyoruz.
const BudgetContext = createContext<BudgetContextType | undefined>(undefined);

// BudgetProvider'ı tanımlıyoruz. `children` parametresini alabilmesi için tipini belirtmemiz gerek.
export const BudgetProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [expenses, setExpenses] = useState<Expense[]>([]);
  const [income, setIncome] = useState<Income[]>([]);
  const [categories, setCategories] = useState<string[]>([]);
  const [budgetLimits, setBudgetLimits] = useState<{ [key: string]: number }>({});

  return (
    <BudgetContext.Provider value={{
      expenses, setExpenses,
      income, setIncome,
      categories, setCategories,
      budgetLimits, setBudgetLimits,
    }}>
      {children} {/* children burada geçiyor */}
    </BudgetContext.Provider>
  );
};

// `useBudget` hook'u ile, context değerlerine erişim sağlıyoruz.
export const useBudget = (): BudgetContextType => {
  const context = useContext(BudgetContext);
  if (!context) {
    throw new Error('useBudget must be used within a BudgetProvider');
  }
  return context;
};